
html,body{height:100%
}
body{display:-ms-flexbox;display:flex
}
#app{min-height:100%;display:-ms-flexbox;display:flex;-ms-flex-flow:column nowrap;flex-flow:column nowrap;-ms-flex:1;flex:1;transition:all .4s cubic-bezier(0.25, 0.8, 0.25, 1)
}
#app #app-container .padded{padding:0 8px
}
#app #app-container .help-block{font-style:italic
}
#app #app-container .help-block.success{color:#4caf50
}
#app #app-container .help-block.warn{color:#ff5722
}
#app #app-container .fluid{width:100%;height:100%
}
#app #app-container .fluid .md-card{border:none;box-shadow:none
}
#app #app-container .CodeMirror{width:100%;height:482px
}



